.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: var(--blue);
  padding: 4rem;
  gap: 4rem;
  width: 100%;
  color: var(--white);
}

.footer .content {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  text-align: center;
  justify-content: center;
}

.icon {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
  padding: 2rem;
  transition: 0.2s;
}

.icon > *:hover {
  cursor: pointer;
  filter: invert(0.9);
}

.detail-page {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 4rem;
}

.page {
  @apply text-center text-2xl;
}

.page h3 {
  @apply opacity-90 duration-200;
}

.page p {
  @apply hover:cursor-pointer text-slate-100 opacity-70 hover:opacity-100 transition-all;
}

.page-list p {
  @apply opacity-70 duration-200;
  @apply hover:opacity-100 hover:cursor-pointer;
}

.footer img {
  max-width: 8rem;
}

hr {
  color: var(--white);
}

@media screen and (max-width: 768px) {
  .footer {
    flex-direction: column;
  }
}
@media screen and (max-width: 480px) {
  .detail-page {
    flex-direction: column;
  }
}
