.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em 4em;
}

.nav-mobile {
  display: none;
  position: sticky;
  padding: 1em 4em;
  justify-content: space-between;
  height: 75px;
  align-items: center;
  background-color: white;
  color: var(--black);
  z-index: 100;
  transition: 0.2s;
  @apply shadow-lg shadow-slate-700/10;
}

.scrolled {
  @apply fixed transition-all w-full;
  animation: transisi 0.3s ease;
}
@keyframes transisi {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

.nav-mobile.active {
  background-color: var(--blue);
  color: var(--white);
}

.nav-dropdown {
  color: black;
  font-size: 3em;
}

.nav-dropdown-content {
  @apply fixed flex justify-center flex-col p-8 pt-12 top-[75px] rounded-bl-2xl rounded-br-2xl bg-white shadow-xl shadow-slate-800/20 w-full z-[190];
  @apply transition-all duration-200;
}

@keyframes nav-dropdown {
  0% {
    transform: translateY(-10em);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.menu-content li {
  @apply list-none px-4 py-2 mb-2 text-xl text-slate-600 text-left  min-w-[50%] rounded-xl transition-all;
  @apply hover:bg-blue-700/20;
  animation: 0.2s nav-dropdown;
}

.nav-dropdown-content .dropdown-menu {
  @apply font-bold text-slate-900 text-left text-2xl p-4 mb-2 rounded-xl border-2 border-blue-700/10;
  @apply hover:bg-blue-700/20 hover:cursor-pointer;
  @apply transition-all;
}

.menu-content {
  height: auto;
  overflow: hidden;
  z-index: -1;
}

.menu-content.hidden {
  height: 0;
  padding: 0;
}

.menu-content.hidden li {
  visibility: hidden;
  transform: translateY(-5em);
  transition: 0.2s;
}

.dropdown-button {
  @apply bg-blue-800 border rounded-2xl p-4 mt-4 w-full text-slate-50 text-2xl font-bold;
  transition: 0.2s;
}

.dropdown-button:hover {
  background-color: var(--white-blue);
  color: var(--blue);
  cursor: pointer;
}

* > * a {
  color: var(--black);
  text-decoration: none;
}
.logo {
  margin-right: 5em;
}
.logo img {
  max-height: 4em;
  transform: scale(1.5) translateY(1em);
  background-color: transparent;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  /* padding: 0.5em; */
}
.logo.scrolledLogo img {
  transform: scale(1) translateY(0);
  animation: 0.3s transisi;
}
.link {
  display: flex;
  align-items: center;
  max-height: 100px;
  list-style: none;
  font-weight: bold;
  color: var(--white-min);
  font-size: 1.25em;
}
.link .active {
  color: var(--white);
  border-bottom: 0.2em solid var(--white);
}
.link .link-txt:hover {
  color: var(--red);
  cursor: pointer;
  border-bottom: 0.2em solid var(--red);
}
.link .link-txt {
  color: var(--white);
  margin: 0 1em;
  padding: 1em 0;
  transition: 0.2s;
}
.login {
  display: flex;
  justify-content: center;
  height: 100%;
  align-content: center;
}
.login .button {
  border: none;
  border-radius: 0.75em;
  padding: 0.5em 1em;
  margin: 0;
  background-color: var(--white);
  font-weight: bold;
  font-size: 1.25em;
  color: var(--blue);
  transition: 0.2s;
}
.login .button:hover {
  background-color: var(--white-blue);
  cursor: pointer;
  box-shadow: 0.5em 0.5em 3em var(--shadow-light);
  color: var(--black-blue);
}
.dropdown {
  position: relative;
}
.popup {
  position: absolute;
  background-color: var(--white);
  border-radius: 0.5em;
  color: var(--black-blue);
  padding: 0.5em;
  box-shadow: 1em 1em 4em var(--shadow-dark);
  animation: popup 0.2s;
  z-index: 100;
}
.popup-link {
  padding: 1em 2em;
  border-radius: 0.5em;
  transition: 0.1s;
  color: var(--black-blue);
}
.popup-link:hover {
  background-color: var(--white-blue);
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .nav {
    display: none;
  }
  .nav-mobile {
    display: flex;
  }
  .logo img {
    max-height: 4em;
    transform: scale(1) translateY(0);
    background-color: transparent;
    box-shadow: 0 0 0 transparent;
    /* padding: 0.5em; */
  }
}

@keyframes popup {
  0% {
    transform: translateY(-5em);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
  }
}
