.caleg {
  @apply md:px-24 md:py-24 sm:p-12 p-2 pt-8; 
  /* padding: 6rem; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  gap: 5em;
  height: 100%;
  background-color: var(--white);
}

.card {
  @apply sm:w-60 w-[80%];
  background-color: white;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  transition: 0.2s;
  box-shadow: 1em 1em 2em var(--shadow-semi-dark);
}

.card:hover {
  cursor: pointer;
  filter: brightness(0.9);
}

.card > img {
  max-height: 15rem;
  @apply sm:max-h-56 max-h-[40rem];
  object-fit: cover;
  border-radius: 2em;
  width: 100%;
}

.card > .nomer {
  background-color: var(--blue);
  color: var(--white);
  position: relative;
  display: flex;
  width: fit-content;
  margin: 1em 0;
  justify-content: center;
  transform: translateX(-1em);
  border-radius: 1rem;
  font-weight: bold;
  padding: 0.75rem;
  display: flex;
  @apply sm:text-lg text-2xl; 
}

.card > .nama {
  display: flex;
  justify-content: center;
  font-size: 1.75em;
  color: var(--black);
  font-weight: bold;
  padding: 0 1em;
  margin-bottom: 1em;
  text-transform: uppercase;
}

.card-detail {
  @apply md:p-6 md:pt-10 sm:p-20 p-10;
  display: flex;
  flex-direction: row;
  background-color: white;
  border-radius: 2rem;
  box-shadow: 1em 1em 2em var(--shadow-semi-dark);
}

.card-detail > .detail-img {
  @apply md:w-[50%] w-[100%];
  display: flex;
  /* width: 50%; */
  justify-content: center;
}

.card-detail img {
  @apply md:w-[75%] w-[50%];
  /* width: 75%; */
  object-fit: contain;
  filter: drop-shadow(0 2em 2em var(--shadow-dark));
}

.card-detail > .detail {
  flex-basis: 100%;
}

.detail-table td,
tr {
  /* padding: 0.5em; */
  /* font-size: 1.25em; */
  /* color: var(--black-blue); */
  text-align: left;
}

.detail-table .subjek {
  font-weight: bold;
  width: 10%;
  color: var(--black);
}

.detail > .close-preview {
  padding: 1em 2em;
  background-color: var(--blue);
  border: transparent;
  border-radius: 1em;
  color: var(--white);
  font-weight: bold;
  width: 100%;
  transition: 0.2s;
}

.close-preview:hover {
  background-color: var(--white-blue);
  color: var(--blue);
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .card-detail {
    flex-direction: column;
  }
  .card-detail {
    gap: 4em;
  }
  .caleg {
    /* padding: 4em; */
  }
}

@media screen and (max-width: 480px) {
  .caleg {
    /* padding: 2em; */
  }
  .detail-table td {
    display: flex;
  }
}

@media screen and (max-width: 1024px) {
  .card-detail {
    width: 100%;
  }
}
