video {
  object-fit: cover;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: -1;
  transform: translateY(75px);
  filter: brightness(0.9);
}

.hero-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  object-fit: contain;
}

.hero-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: inherit;
  padding: 1em;
  color: var(--white);
  align-items: center;
}

.hero-content h1 {
  text-shadow: 0 0 10px #0005;
  text-align: center;
}

